	module.exports = {"data":{"post":{"html":"<p><a href=\"http://photos.shcon.com/view.php?dispsize=640&album=07_2001%2F20010724-crh&pic=dscn0066.jpg&start=0&picindex=1\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/08/crhsprinkler.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crhsprinkler.jpg: \" /></a></p>\n<p>This picture is not new, but it's become one of our favorites. Mama ordered mouse pads with this picture for us, so now all day at work I can watch Christopher try to catch the rain.</p>\n<p>The Telekom is now claiming that DSL is available on our street. A couple weeks ago their datbase was still saying beginning of November. I'm not sure, though,  where the \"Central Office\" for our exchange is located... if it is in the middle of Jesteburg, we might be too far away. But I re-ordered the beginning of this week, and they haven't sent a rejection yet. Or I can be like Cringely and <a href=\"http://www.pbs.org/cringely/pulpit/pulpit20010823.html\">roll my own DSL</a>.</p>\n<p><a href=\"http://andrea.editthispage.com\">Andrea</a> has the last of her last written exams out the way, and is now relaxing over the weekend with her folks. Hey, if you happen to be in Bremen tomorrow, we might run into each other...</p>","fields":{"title":"Hot and Sticky","date":"24 Aug 2001","url":"https://papascott.de/2001-08-24-hot-and-sticky/"}}},"pathContext":{"slug":"/2001-08-24-hot-and-sticky/","prev":{"fields":{"title":"Fuel Efficiency","date":"23 Aug 2001","slug":"/2001-08-23-fuel-efficiency/"}},"next":{"fields":{"title":"Our Man in Havanna","date":"25 Aug 2001","slug":"/2001-08-25-our-man-in-havanna/"}}}}

